@import url(https://fonts.googleapis.com/css2?family=Elsie:wght@900&family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Poppins", sans-serif;
  padding: 3.5rem 0;
  isolation: isolate;
}

h1 {
  font-family: "Elsie", cursive;
  font-size: 3.4rem;
  margin-bottom: 1.5rem;
}

.table--up {
  display: flex;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  margin-bottom: 1.8rem;
}

.btn__add {
  background-color: #19a548;
}
.search {
  -webkit-appearance: none;
  border: 1px solid #dadada;
  border-radius: 5px;
  outline: none;
  padding: 8px;
  width: 20rem;
  line-height: 30px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
table {
  border-collapse: collapse;
  border-spacing: 1rem;
  box-shadow: #0000001a 0px 4px 12px;
  border: 1px solid #b3b3b3;
  width: 93.75rem;
}

th {
  text-transform: capitalize;
  font-size: 1.135rem;
}

th,
td {
  border-bottom: 1px solid #b3b3b3;
  padding: 0.2rem 1.2rem;
}

td span {
  padding: 6px 0;
}

tbody tr:nth-child(2n + 1){
  background-color: #eee;
}
thead tr {
  background-color: #da1c1c;
  color: white;
}

.action__btn{
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column-gap: 8px;
  -webkit-column-gap: 8px;
          column-gap: 8px;

}

.address--field + .address--field::before {
  content: ",";
  padding: 4px;
}

button {
  display: inline;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  color: white;
  font-size: 0.9rem;
  cursor: pointer;
}

.btn__edit {
  background-color: #0f46dd;
}

.btn__remove {
  background-color: #da1c1c;
}

.skeleton {
  height: 2.7109rem;
  width: 100%;
  background-color: #ececec;
  -webkit-animation: skeleton .8s infinite alternate;
          animation: skeleton .8s infinite alternate;
}

td[colspan='7'] {
  padding: 0 !important;
}

@-webkit-keyframes skeleton {
  to {
    opacity: .1;
  }
}

@keyframes skeleton {
  to {
    opacity: .1;
  }
}

.overlay {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}

.form__container {
  max-width: 450px;
  width: 100%;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #ffffff;
  box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.75);
  border-radius: 8px;
  padding: 3em 2.5em;
}

.from__box {
  padding: 5px 0;
}

.address,
.form__txt {
  display: inline-block;
  min-width: 100px;
  font-weight: 700;
}

.address__title::after,
.form__txt::after {
  content: ":";
  float: right;
  margin-right: 5px;
}

.address {
  margin-top: 13px;
}

.address__part {
  margin-left: 35px;
}

.address__title {
  display: inline-block;
  min-width: 64px;
}

.input__field {
  -webkit-appearance: none;
  border: 1px solid #dadada;
  border-radius: 5px;
  padding: 0 10px;
  outline: none;
  width: 13.7rem;
  line-height: 30px;
}

form .from__box:nth-child(5) {
  margin: 13px 0 30px;
}

.form__submit {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.form__submit input[type="submit"] {
  background-color: #19a548;
  padding: 10px 45px;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  color: white;
  font-weight: 900;
  letter-spacing: 1.2px;
  outline: none;
}

.form__submit input[type="button"] {
  background-color: #eeeeee;
  padding: 10px 25px;
  border: 1px solid;
  border-radius: 5px;
  font-size: 1.2rem;
  color: #000000;
  font-weight: 900;
  letter-spacing: 1.2px;
  outline: none;
}
